import BackingUtil from "./backingUtil";
import CacheUtil from "./cacheUtil";

export const PITCH_MAX = 60;

export const getKeyIndexFromPitcher = (index: number) => {
    return (PITCH_MAX - 1 - index + 9) % 12;
}

export const getNormalizeLenFromNotes = (notes: MelodyLength) => {
    let total = 0;
    total += notes.len4;
    total += notes.len8 / 2;
    total += notes.len16 / 4;
    total += notes.len4div3 / 3;
    total += notes.len8div3 / 6;
    return total;
}

export const getAdditionNotsLen = (notes1: MelodyLength, notes2: MelodyLength) => {
    return getSortetdNotesLen({
        len4: notes1.len4 + notes2.len4,
        len8: notes1.len8 + notes2.len8,
        len16: notes1.len16 + notes2.len16,
        len4div3: notes1.len4div3 + notes2.len4div3,
        len8div3: notes1.len8div3 + notes2.len8div3
    });
}

export const getSortetdNotesLen = (base: MelodyLength) => {

    const len8div3Cnt3 = Math.floor(base.len8div3 / 3);
    base.len8div3 -= len8div3Cnt3 * 3;
    base.len8 += len8div3Cnt3;

    const len8div3Cnt2 = Math.floor(base.len8div3 / 2);
    base.len8div3 -= len8div3Cnt2 * 2;
    base.len4div3 += len8div3Cnt2;

    const len4div3Cnt3 = Math.floor(base.len4div3 / 3);
    base.len4div3 -= len4div3Cnt3 * 3;
    base.len4 += len4div3Cnt3;

    const len16Cnt2 = Math.floor(base.len16 / 2);
    base.len16 -= len16Cnt2 * 2;
    base.len8 += len16Cnt2;

    const len8Cnt2 = Math.floor(base.len8 / 2);
    base.len8 -= len8Cnt2 * 2;
    base.len4 += len8Cnt2;

    return base;
}

export const getNotesLenFromQuantize = (quantize: number, count: number): MelodyLength => {

    let len4 = 0;
    let len8 = 0;
    let len16 = 0;
    let len4div3 = 0;
    let len8div3 = 0;

    switch (quantize) {
        case 1: len4 = count; break;
        case 2: len8 = count; break;
        case 3: len4div3 = count; break;
        case 4: len16 = count; break;
        case 6: len8div3 = count; break;
    }
    return getSortetdNotesLen({
        len4, len8, len16, len4div3, len8div3
    });
}
