import { BeatSignature, ChordElement, DetailChord, DetailFixed, DetailInit, DetailSection, ScoreData } from "../../../../redux/store/score/scoreData";

/**
 * 
 */
namespace ProgUtil {

    /**  */
    export type BuildType = 'blank' | 'custom' | 'template';

    export const STEP_LIST = [0, 2, 3, 4] as const;
    export type Step = typeof STEP_LIST[number];

    /**
     * 
     */
    export type BlockProps = {
        label: DetailSection | DetailFixed;
        chordList?: DetailChord[];
    }

    export const DEGREE_FILTER_LIST = [
        '', '', '', '', 'm', 'm', 'm'
    ] as const;
    export type DegreeFilter = typeof DEGREE_FILTER_LIST[number];

    /**
     * 
     * @returns 
     */
    export const getInitialBlockList = (): BlockProps[] => {
        return [
            { label: { fixed: "Start" } },
            { label: { fixed: "End" } },
        ];
    }

    /**
     * Blank
     * @returns 
     */
    export const getBlankBlockList = (): BlockProps[] => {
        return [
            { label: { fixed: "Start" } },
            {
                label: { labelName: "Section" }, chordList: [
                    getDefaultChord()
                ]
            },
            { label: { fixed: "End" } },
        ];
    }

    export const getDefaultChord = (): DetailChord => {
        return {
            beatLen: 4, minute: { head: 0, tail: 0 }, root: null, on: null, symbolKey: 'major',
            backing: {
                voicingList: [],
                pattern: null,
                hasError: false
            }
        }
    }

    /**
     * 
     * @param beat 
     * @returns 
     */
    export const getFilteredStepList = (beat: BeatSignature) => {
        return STEP_LIST.filter((item) => {
            // 0
            if (item === 0) return true;

            switch (beat) {
                case '4/4':
                case '6/8': {
                    if (item === 3) return false;
                }
                    break;
                case '3/4':
                case '9/8': {
                    if (item !== 3) return false;
                }
                    break;
                case '2/4':
                case '6/8': {
                    if (item !== 2) return false;
                }
                    break;
            }
            return true;
        });
    }

    /**
     * 
     * @param blockList 
     * @returns 
     */
    export const buildScoreData = (blockList: BlockProps[]): ScoreData => {
        const elementList: ChordElement[] = [];
        // 
        const initElement: DetailInit = {
            keyIndex: 0,
            bpm: 100,
            scale: "major",
            beatSignature: "4/4"
        }
        elementList.push({ type: 'init', detail: initElement });
        blockList.forEach((block) => {
            if (block.chordList == undefined) {
                // 
                elementList.push({ type: 'fixed', detail: block.label });
            } else {
                // 
                elementList.push({ type: 'section', detail: block.label });
                block.chordList.forEach((chord) => {
                    elementList.push({ type: 'chord', detail: chord });
                });
            }
        });
        return { chordList: elementList, notesList: [] };
    }
}

export default ProgUtil;