import { useContext, useMemo } from "react";
import styled from "styled-components";
import Styles from "../../../../base/design/styles";
import { GlobalContext } from "../../../entry/systemEntry";

const ProgTemplateFrame = (props: {
}) => {
    const { store, dispatcher } = useContext(GlobalContext);


    const templateJsxList = useMemo(() => {
        return ['short major 01', 'short major 02', 'short minor 01'].map((val, i) => {
            return (
                <_Item key={i}>{val}</_Item>
            );
        });
    }, []);

    return (
        <_Frame>
            {templateJsxList}
        </_Frame>
    );
}

export default ProgTemplateFrame;


const _Frame = styled.div<{
}>`
    display: inline-block;
    width: 100%;
    height: 100%;
`;


const _Item = styled.div<{
}>`
    ${Styles.CSS_LABEL_MIDIUM}
    display: inline-block;
    width: 100%;
    background-color: #87b4b4;
    text-align: left;
    margin: 2px 0 0 0;
    padding: 0 0 0 5px;
    color: #eb0000;
`;
