import { useContext, useEffect, useMemo, useState } from "react";
import styled, { css } from "styled-components";
import { ExecutableButton } from "../../../base/components/button/executableButton";
import { _CSS_LABEL_MIDIUM } from "../../../base/design/styles";
import { ChordBacking, DetailChord, DetailFixed, DetailInit, DetailSection, ScoreData } from "../../../redux/store/score/scoreData";
import CacheUtil from "../../../utils/cacheUtil";
import DataUtil from "../../../utils/dataUtil";
import HarmonizeUtil from "../../../utils/harmonizeUtil";
import TheoryUtil from "../../../utils/theoryUtil";
import { GlobalContext } from "../../entry/systemEntry";
import ChordProgDialog from "./chordProg/chordProgDialog";
import ScoreBuilder from "./scoreBuilder";

const ManageTab = () => {

    const { store, dispatcher } = useContext(GlobalContext);

    const [file, setFile] = useState<null | File>(null);

    const fileHandle = store.scoreState.fileHandle;
    const data = store.scoreData;
    const tabIndex = store.systemState.scoreTabIndex;

    const [fileInfo, scoreInfo]: [{
        name: string;
        size: string;
        modified: string;
    }, {
        key: string;
        tempo: string;
        rate: string;
        section: string;
        chord: string;
        bar: string;
        time: string;
        notes: string;
    }] = useMemo(() => {
        let name = '-';
        let size = '-';
        let modified = '-';
        let key = '-';
        let tempo = '-';
        let rate = '-';
        let section = '-';
        let chord = '-';
        let bar = '-';
        let time = '-';
        let notes = '-';
        if (tabIndex === 0) {
            if (file != null) {
                name = file.name;
                size = `${file.size.toString()} byte`;
                modified = DataUtil.getStringFromDate(new Date(file.lastModified));
            }
            if (data.chordList.length > 0) {
                const init = data.chordList[0].detail as DetailInit;
                key = `${TheoryUtil.KEY12_MAJOR_SCALE_LIST[init.keyIndex]} ${init.scale}`;
                tempo = init.bpm.toString();
                rate = init.beatSignature;
                let sectionNum = 0;
                let chordNum = 0;
                data.chordList.forEach(chord => {
                    switch (chord.type) {
                        case 'section': sectionNum++; break;
                        case 'chord': chordNum++; break;
                    }
                });
                section = sectionNum.toString();
                chord = chordNum.toString();

                const cacheList = store.scoreState.elementCacheList;
                if (cacheList.length > 0) {
                    const last = cacheList[cacheList.length - 1];
                    time = `${last.pastSecond / 1000}s`;
                    bar = `${last.bar} (${last.surplusBeat})`;
                }
                notes = data.notesList.length.toString();
            }
        }
        return [{
            name, size, modified
        }, {
            key, tempo, rate, section, chord, bar, time, notes
        }]
    }, [tabIndex, data, file]);

    useEffect(() => {
        if (tabIndex === 0 && fileHandle != null) {
            fileHandle.getFile().then(file => {
                setFile(file);
            });
        }
    }, [tabIndex]);

    const setScoreData = ((data: ScoreData) => {
        store.scoreState.focusIndex = 2;
        store.scoreState.distIndex = store.scoreState.focusIndex;
        dispatcher.score.setScoreState({ ...store.scoreState });
        // dispatcher.score.setScoreTabIndex(1);
        dispatcher.score.setScoreData(data);
        // 
        store.scoreData = data;
        CacheUtil.updateHarmonizeCache(store, dispatcher);
    });

    /**
     * 
     * @param labelName 
     * @param labelValue 
     * @returns JSX
     */
    const createLabelRecord = (labelName: string, labelValue: string) => {
        return (
            <_Recored>
                <_ItemnLabel>{labelName}</_ItemnLabel>
                <_ItemnValue>{labelValue}</_ItemnValue>
            </_Recored>
        );
    }


    const searchLocalFileAction = () => {
        (async () => {
            const [fileHandle] = await window.showOpenFilePicker();
            const file = await fileHandle.getFile();
            const fileContents = await file.text();
            // console.log(fileContents);
            store.scoreState.fileHandle = fileHandle;
            setFile(file);
            const text = DataUtil.unZip(fileContents);
            // const text = fileContents;
            console.log(text);
            setScoreData(JSON.parse(text));
        })();
    }

    const disconnectAction = () => {
        store.scoreState.fileHandle = null;
        dispatcher.score.setScoreState({ ...store.scoreState });
        // 
        store.scoreData = data;
        CacheUtil.updateHarmonizeCache(store, dispatcher);
        setFile(null);
    };

    return (
        <_Wrap>
        <_ButtonsDiv>
            <_Button onClick={()=> {
                store.systemState.dialog = <ChordProgDialog setScoreData={setScoreData}/>;
                dispatcher.system.setState(store.systemState);
            }}>new score</_Button>
            <_Button onClick={searchLocalFileAction}>load file</_Button>
            <_Button onClick={searchLocalFileAction}>search local</_Button>
            <_Button onClick={disconnectAction}>disconnect</_Button>
        </_ButtonsDiv>
            <_MainDiv>
                <_Left>
                    <_ListInfoDiv isEnable={true}>

                    </_ListInfoDiv>
                    {/* <_Link onClick={() => {

                    const data: ScoreData = {
                        chordList: [
                            { type: 'init', detail: { keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' } as DetailInit },
                            { type: 'fixed', detail: { fixed: 'Start' } as DetailFixed },
                            { type: 'section', detail: { labelName: 'Section' } as DetailSection },
                            {
                                type: 'chord', detail: {
                                    beatLen: 4, minute: { head: 0, tail: 0 }, root: null, on: null, symbolKey: 'major',
                                    backing: {
                                        voicingList: [],
                                        pattern: null,
                                        hasError: false
                                    }
                                } as DetailChord
                            },
                            { type: 'fixed', detail: { fixed: 'End' } as DetailFixed },
                        ],
                        notesList: []
                    };
                    // setSource(JSON.stringify(data));
                    setData(data);
                }} >Create an empty score.</_Link>
                <_Link onClick={() => {
                    const builder = new ScoreBuilder({ keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' });
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 2);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 1);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 2);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 1);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    // setSource(JSON.stringify(builder.getScoreData()));
                    setData(builder.getScoreData());
                }} >mm</_Link>
                <_Link onClick={() => {
                    const builder = new ScoreBuilder({ keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' });
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 0);
                    builder.addChordDiatonic(2, 5);
                    builder.addChordDiatonic(2, 3);
                    builder.addChordDiatonic(2, 4);
                    builder.addChordDiatonic(2, 5);
                    // setSource(JSON.stringify(builder.getScoreData()));
                    setData(builder.getScoreData());
                }} >mm</_Link>
                <_Link onClick={() => {
                    const builder = new ScoreBuilder({ keyIndex: 0, bpm: 100, scale: 'major', beatSignature: '4/4' });
                    builder.addChordDiatonic(4, 3);
                    builder.addChordDiatonic(4, 4);
                    builder.addChordDiatonic(4, 2);
                    builder.addChordDiatonic(4, 5);
                    builder.addChordDiatonic(4, 1);
                    builder.addChordDiatonic(4, 4);
                    builder.addChord(4, { index: 0, isFlat: false }, 'suspend4th');
                    builder.addChordDiatonic(4, 0);
                    // setSource(JSON.stringify(builder.getScoreData()));
                    setData(builder.getScoreData());
                }} >mmmsus4</_Link> */}
                </_Left>
                <_Right>
                    {/* <_Source value={source} onChange={e => setSource(e.target.value)} /> */}

                    <_DataInfoDiv isEnable={fileHandle != null}>
                        <_TitleLabel>DATA</_TitleLabel>
                        {createLabelRecord('file', fileInfo.name)}
                        {createLabelRecord('name', fileInfo.name)}
                        {createLabelRecord('url', fileInfo.name)}
                        {createLabelRecord('size', fileInfo.name)}
                        {/* {createLabelRecord('size', fileInfo.size)}
                        {createLabelRecord('modified', fileInfo.modified)} */}
                    </_DataInfoDiv>
                    <_ScoreInfoDiv isEnable={data.chordList.length > 0}>
                        <_TitleLabel>SCORE</_TitleLabel>
                        {createLabelRecord('key', scoreInfo.key)}
                        {createLabelRecord('tempo', scoreInfo.tempo)}
                        {createLabelRecord('rate', scoreInfo.rate)}
                        {createLabelRecord('section', scoreInfo.section)}
                        {createLabelRecord('chord', scoreInfo.chord)}
                        {createLabelRecord('bar', scoreInfo.bar)}
                        {createLabelRecord('time', scoreInfo.time)}
                        {createLabelRecord('notes', scoreInfo.notes)}
                    </_ScoreInfoDiv>
                </_Right>
            </_MainDiv>
        </_Wrap>
    );
}

export default ManageTab;

const _Wrap = styled.div`
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: #7e7e7e;
    text-align: left;
`;

const _MainDiv = styled.div`
    display: inline-block;
    position: relative;
    width: 100%;
    height: calc(100% - 40px);
`;

const _Left = styled.div`
    display: inline-block;
    position: relative;
    width: 50%;
    height: 100%;
    /* background-color: #8a6b6b; */
    text-align: left;
    vertical-align: top;
`;

const _Right = styled.div`
    display: inline-block;
    position: relative;
    width: 50%;
    height: 100%;
    /* background-color: #969696; */
    text-align: left;
    vertical-align: top;
`;

const _Link = styled.div`
    
    display: inline-block;
    width: calc(100% - 50px);
    height: 50px;
    margin: 20px 25px;
    border: 2px solid #cacaca;
    /* background: linear-gradient(to bottom, #585858, #a7a7a7, #585858); */
    background-color: #000;
    color: #ccc;
    font-size: 30px;
    font-weight: 600;
    text-align: left;
    box-sizing: border-box;
    padding-left: 20px;
    border-radius: 6px;

    &:hover {
        background-color: #616161;
    }
`;

